import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Snackbar from 'material-ui/Snackbar';
import IconButton from 'material-ui/IconButton';
import CloseIcon from 'material-ui-icons/Close';

const styles = theme => ({
  close: { width: theme.spacing.unit * 4, height: theme.spacing.unit * 4 },
  merah: { '& > div': { background: '#CC5251' }},
  hijau: { '& > div': { background: '#50C650' }},
});

const SimpleSnackbar = props => {
  const { classes, open, handleClose, snackBarMsg } = props;
  return (
    <div>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={open}
        autoHideDuration={2000}
        onClose={handleClose}
        SnackbarContentProps={{ 'aria-describedby': 'message-id' }}
        className={snackBarMsg ? classes.hijau : classes.merah}
        message={<span id="message-id">{snackBarMsg ? 'Sukses' : 'Password Yang Anda Masukkan Salah'}</span>}
        action={[
          <IconButton
            key="close"
            aria-label="Close"
            color="inherit"
            className={classes.close}
            onClick={handleClose}
          >
            <CloseIcon />
          </IconButton>,
        ]}
      />
    </div>
  );
};

SimpleSnackbar.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool,
  snackBarMsg: PropTypes.bool,
  handleClose: PropTypes.func,
};

export default withStyles(styles)(SimpleSnackbar);



